.section_title, 
.section_subtitle {
    text-align: center;
}
.about_container {
    grid-template-columns:  repeat(2, 1fr);
    align-items: center;
    column-gap: 4rem;

    .about_img {
        width: 350px;
        border-radius: 1.5rem;
        justify-self: center;
    }
    .about_data {
        .about_description {
            padding: 0;
            margin-bottom: 2.5rem;
            font-size: 0.813rem;
            b {
                font-size: 0.813rem;
            }
        }
    }
}


@media screen and (max-width:992px){
    .about_container {
        grid-template-columns: 1fr;
        row-gap: 2.5rem;
    }
    .about_img {
        width: 220px;
    }
    .about_data {
        text-align: center;
    }
    .about_description {
        padding: 0 5rem;
        margin-bottom: 2rem;
    }
}

@media screen and (max-width:576px){
    .about_description {
        padding: 0;
    }
}
